import React from 'react';
import { NavLink } from 'react-router-dom';
import { mainPath, aboutPath, catalogPath, productPath, basketPath } from 'helpers/routes';

const Navigation = () => (
  <ul>
    <li><NavLink exact to={mainPath()}> </NavLink></li>
    <li><NavLink to={aboutPath()}></NavLink></li>
    <li><NavLink to={catalogPath()}></NavLink></li>
    <li><NavLink to={basketPath()}></NavLink></li>
  </ul>
);

export default Navigation;
